#wpfooter {
  background-color: #f6f7f7;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.05);
  border-top: 1px solid #c3c4c7;
  padding: 15px 25px;
}
#wpfooter .dashicons {
  line-height: 1.1em;
}

[wplng-help-box],
[wplng-help-box-right] {
  font-family: dashicons;
  display: inline-block;
  line-height: 1.2em;
  font-weight: 400;
  font-style: normal;
  speak: never;
  text-decoration: inherit;
  text-transform: none;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 20px;
  height: 20px;
  font-size: 20px;
  vertical-align: top;
  text-align: center;
  transition: opacity 0.2s ease-in;
  transition: color 0.2s ease-in;
  vertical-align: text-bottom;
  cursor: pointer;
  color: #3c434a;
  opacity: 0.25;
}
[wplng-help-box]:hover,
[wplng-help-box-right]:hover {
  opacity: 1;
}
[wplng-help-box]::before,
[wplng-help-box-right]::before {
  content: "\f223";
}

[wplng-help-box-right] {
  float: right;
}

.wplng-help-box {
  display: none;
  padding: 2px 10px 0;
  border: 1px solid #69a8bb;
  border-radius: 3px;
  background-color: rgba(105, 168, 187, 0.1);
}
.wplng-help-box hr {
  border-top: 1px solid #69a8bb;
  border-bottom: unset;
}
.wplng-help-box code {
  background-color: rgba(60, 67, 74, 0.075);
  border-radius: 3px;
}
.wplng-help-box pre {
  white-space: pre-wrap;
  background-color: rgba(60, 67, 74, 0.075);
  border-radius: 3px;
  padding: 10px;
  border: 1px solid #69a8bb;
}

.wplng-fe-50 {
  min-width: 49%;
  display: inline-block;
}

.wplng-option-page-title {
  background-color: #f6f7f7;
  font-size: 20px;
  margin: 0 0 0 -20px;
  padding: 15px 25px;
  line-height: 22px;
  font-weight: 500;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.05);
  border-bottom: 1px solid #c3c4c7;
  text-align: center;
}
.wplng-option-page-title .dashicons {
  font-size: 1.1em;
  line-height: inherit;
  width: 1.1em;
  height: 1.1em;
}

.wplng-icon-button {
  width: 45px;
  text-align: center;
}
.wplng-icon-button .dashicons {
  line-height: 1.4em;
}

#wpbody label {
  vertical-align: baseline;
}

#nri-slug-wrapper,
.notice:not(.wplng-notice) {
  display: none !important;
}

.wplng-form-table {
  padding-right: 200px;
  border-collapse: initial;
  margin-top: 30px;
}
.wplng-form-table * {
  box-sizing: border-box;
}
.wplng-form-table a {
  text-decoration: none;
}
.wplng-form-table a:focus {
  box-shadow: none;
  outline: none;
}
.wplng-form-table a:hover {
  text-decoration: underline;
}
.wplng-form-table td {
  padding: 15px;
  background-color: #f6f7f7;
  display: block;
  border: 1px solid #c3c4c7;
  margin-bottom: 25px;
  max-width: 540px;
  margin-right: auto;
  margin-left: auto;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.05);
  border-radius: 3px;
}
.wplng-form-table td .button {
  vertical-align: bottom;
}
.wplng-form-table td .button:focus, .wplng-form-table td .button:hover {
  text-decoration: none;
}
.wplng-form-table td p {
  margin-bottom: 8px;
}
.wplng-form-table td fieldset {
  display: block;
}
.wplng-form-table .wplng-tr-submit td {
  border: none;
  box-shadow: none;
  background-color: unset;
  padding: 0;
}
.wplng-form-table .wplng-tr-submit td p.submit {
  padding: 0 0 64px;
}
.wplng-form-table .wplng-tr-submit td p.submit #submit {
  width: 100%;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.05);
  min-height: 42px;
  font-size: 14px;
}

@media screen and (max-width: 1120px) {
  #wpbody-content h1 {
    text-align: center;
  }
  .wplng-form-table {
    padding-right: 0;
  }
  .wplng-form-table th,
  .wplng-form-table td {
    max-width: 540px;
    margin-right: auto;
    margin-left: auto;
    display: block;
  }
  .wplng-form-table th {
    padding: 25px 0 20px;
    font-size: 1.2em;
    width: 100%;
  }
  .wplng-form-table td .button.wplng-icon-button {
    vertical-align: top;
    padding: 0;
  }
  .wplng-form-table td .button.wplng-icon-button .dashicons {
    vertical-align: middle;
    line-height: 0.8em;
  }
  .wplng-form-table .wplng-tr-submit th {
    display: none;
  }
  .wplng-form-table .wplng-tr-submit td p.submit {
    padding: 10px 0 42px;
    font-size: 14px;
  }
}
#wplng_link_media_entries {
  width: 100%;
  height: 150px;
}

.wplng-flag {
  max-width: 1.5em;
  max-height: 1.5em;
  height: auto;
  margin-right: 0.4em;
  vertical-align: text-top;
  display: inline;
}

#wplng-section-entry-edit input[type=text],
#wplng-link-media-entry-new input[type=text] {
  overflow: hidden;
  width: 100%;
  resize: none;
}

p.submit,
#submit {
  display: none;
}

#wplng-link-media-entries {
  margin-top: 5px;
}
#wplng-link-media-entries .wplng-link-media-entry {
  border: 1px solid #c3c4c7;
  padding: 5px 10px 8px 6px;
  margin-bottom: 15px;
  background-color: #fff;
  border-radius: 3px;
}
#wplng-link-media-entries .wplng-link-media-entry .wplng-rule-header {
  display: flex;
  justify-content: space-between;
}
#wplng-link-media-entries .wplng-link-media-entry .wplng-rule-header .wplng-rule-link-remove::before {
  content: "|";
  display: inline;
  color: #8c8f94;
  text-decoration: none;
  margin-right: 5px;
  font-weight: lighter;
}

#wplng-new-rule-button {
  width: 100%;
  text-align: center;
}

#wplng-link-media-entry-new #wplng-new-action-section {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

#wplng-link-media-entry-edit #wplng-edit-action-section {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.wplng-link-media-mode {
  margin: 6px 0;
}
.wplng-link-media-mode label {
  padding-right: 15px;
}